<template>
	<div id="foodSearch">
		<back id="back"></back>
		<div id="foodSearchTitle">
			<div id="foodSearchTitleParams">"{{this.food1}}"</div>
			<div id="foodSearchTitleWord">美食搜索结果</div>
		</div>
		<food-list :foodResult="food2" v-if="food2"></food-list>
		<div v-else>未找到相关美食</div>
	</div>
</template>

<script>
	import back from "../../../../../views/back.vue"
	import foodList from '../../foodList.vue'
	import about from "../../../../../views/About.vue"
	var AV=about.AV;
	export default{
		name : 'foodSearch',
		data(){
			return{
				food1:'',
				food2:''
			}
		},
		components:{
			foodList,
			back
		},
		mounted(){
			this.food1=this.$route.query.food1;
			
			var that=this;
			console.log(that.food1);
			var query=new AV.Query('foods');
			query.equalTo('country',that.food1);
			query.find().then(function(data){
				that.food2=JSON.parse(JSON.stringify(data));
				console.log(that.food2);
			})
		}
	}
</script>

<style scoped="scoped">
	#back{
		position: absolute;
		top: 250px;
		left: 40%;
	}
	#foodSearch{
		width: 100%;
		height: 100%;
	}
	#foodSearchTitle{
		margin: 30px 0 20px 14%;
		width: 300px;
		height:60px;
		background-color: #01DFA5;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	#foodSearchTitleParams{
		width: 40%;
		font-size: 25px;
		color: #DA81F5;
	}
	#foodSearchTitleWord{
		width: 50%;
		color: red;
		font-size: 20px;
	}
</style>
